import { Component, OnInit } from '@angular/core';
import { TutorialServiceChecklist } from 'src/app/services/tutorial.serviceChecklist';
import { map } from 'rxjs/operators';
import { FilterPipe } from '../../filter.pipe'; 
import * as XLSX from 'xlsx';

const getFileName = (name: string) => {
  let timeSpan = new Date().toISOString();
  let sheetName = name || "ExportResult";
  let fileName = `${sheetName}-${timeSpan}`;
  return {
    sheetName,
    fileName
  };
};

@Component({
  selector: 'app-tutorials-list2',
  templateUrl: './tutorials-list-all-user.component.html',
  styleUrls: ['./tutorials-list-all-user.component.css']
})
export class TutorialsListComponentChecklist implements OnInit {

  tutorials: any;
  currentTutorial = null;
  currentIndex = -1;
  title = '';
  public search:any = '';
  tutorial: any[] = [];
  query:string;
  fileName= 'ExcelSheet.xlsx';
  selectedCount=0;

  constructor(private tutorialService: TutorialServiceChecklist) { FilterPipe }

  ngOnInit(): void {
    this.retrieveTutorials();
  }

  getNowDate() {
    var returnDate = "";
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1; 
    var yyyy = today.getFullYear();
    
   
    if (mm < 10) {
        returnDate += `0${mm}-`;
    } else {
        returnDate += `${mm}-`;
    }

    if (dd < 10) {
      returnDate += `0${dd}-`;
  } else {
      returnDate += `${dd}-`;
  }

    returnDate += yyyy;
    return returnDate;
};

  refreshList(): void {
    this.currentTutorial = null;
    this.currentIndex = -1;
    this.retrieveTutorials();
  }

  retrieveTutorials(): void {
    this.selectedCount=0;
    this.tutorialService.getAll(this.getNowDate()).snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )
    ).subscribe(data => {
      this.tutorials = data;
      for (let i = 0; this.tutorials?.length > i; i++) {
        if(this.tutorials[i].uname!=' ')
        {
          this.selectedCount++;
        }
      }
    });
  }

  setActiveTutorial(tutorial, index): void {
    this.currentTutorial = tutorial;
    this.currentIndex = index;
  }

  
  exportArray() {
    this.tutorialService.getAll(this.getNowDate()).snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )
    ).subscribe(data => {
      this.tutorials = data;
    });

    TutorialsListComponentChecklist.exportArrayToExcel(this.tutorials, "ExampleArray");
  }


  static exportArrayToExcel(arr: TutorialServiceChecklist[], name?: string) {
    let { sheetName, fileName } = getFileName(name);
  
    var wb = XLSX.utils.book_new();
    var ws = XLSX.utils.json_to_sheet(arr);
    XLSX.utils.book_append_sheet(wb, ws, sheetName);
    XLSX.writeFile(wb, `${fileName}.xlsx`);
  }
  
}