import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TutorialsListComponent } from './components/tutorials-list/tutorials-list.component';
import { TutorialsListComponentChecklist } from './components/tutorials-list-all-user/tutorials-list-all-user.component';
import {RiskyUserLisComponent} from './components/risky-user-lis/risky-user-lis.component';
import {PrevDateAnalysisComponent} from './components/prev-date-analysis/prev-date-analysis.component'
import {YemeklistesiComponent} from './components/yemeklistesi/yemeklistesi.component'
import {ServislistesiComponent} from './components/servislistesi/servislistesi.component'

const routes: Routes = [
  { path: '', redirectTo: 'tutorials', pathMatch: 'full' },
  { path: 'tutorials', component: TutorialsListComponent },
  { path: 'add', component: RiskyUserLisComponent },
  { path: 'checkComp', component: TutorialsListComponentChecklist },
  { path: 'prevAnalysis', component: PrevDateAnalysisComponent },
  { path: 'yemekListesi', component: YemeklistesiComponent },
  { path: 'servisListesi', component: ServislistesiComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }