import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import ShuttleService from '../models/shuttle';

@Injectable({
  providedIn: 'root'
})
export class ShuttleModelService {

  private dbPath = '/QrCodes/Servis/';

  shuttleRef: AngularFireList<ShuttleService> = null;

  constructor(private db: AngularFireDatabase) {
    this.shuttleRef = db.list(this.dbPath);
  }

  getAll(selectedDate): AngularFireList<ShuttleService> {
    this.shuttleRef = this.db.list(this.dbPath , ref => ref.orderByChild('relatedDate').equalTo(selectedDate));
    return this.shuttleRef;
  }

}