import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import TutorialChecklist from '../models/tutorialChecklist';


@Injectable({
  providedIn: 'root'
})
export class TutorialServiceChecklist {

  private dbPath = '/checklists';

  tutorialsChecklistRef: AngularFireList<TutorialChecklist> = null;
  
  constructor(private db: AngularFireDatabase) {
    
  }

  getAll(selectedDate): AngularFireList<TutorialChecklist> {
    this.tutorialsChecklistRef = this.db.list(this.dbPath , ref => ref.orderByChild('relatedDate').equalTo(selectedDate));
    return this.tutorialsChecklistRef;
  }
}