import { Component, OnInit } from '@angular/core';
import { TutorialService } from 'src/app/services/tutorial.service';
import { map } from 'rxjs/operators';
import { FilterPipe } from '../../filter.pipe'; // -> imported filter pipe
import * as XLSX from 'xlsx';


const getFileName = (name: string) => {
  let timeSpan = new Date().toISOString();
  let sheetName = name || "ExportResult";
  let fileName = `${sheetName}-${timeSpan}`;
  return {
    sheetName,
    fileName
  };
};

@Component({
  selector: 'app-tutorials-list',
  templateUrl: './tutorials-list.component.html',
  styleUrls: ['./tutorials-list.component.css']
})
export class TutorialsListComponent implements OnInit {

  tutorials: any;
  currentTutorial = null;
  currentIndex = -1;
  title = '';
  public search:any = '';
  tutorial: any[] = [];
  query:string;
  selectedCount=0;
  constructor(private tutorialService: TutorialService) { FilterPipe }

  ngOnInit(): void {
    this.retrieveTutorials();

    
  }

  refreshList(): void {
    this.currentTutorial = null;
    this.currentIndex = -1;
    this.retrieveTutorials();
  }

  retrieveTutorials(): void {

    this.tutorialService.getAll().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )
    ).subscribe(data => {
      this.selectedCount=0;
      this.tutorials = data;
      for (let i = 0; this.tutorials?.length > i; i++) {
        if(this.tutorials[i].Token==' ')
        {
          this.selectedCount++;
        }
      }
    });

  }

  setActiveTutorial(tutorial, index): void {
    this.currentTutorial = tutorial;
    this.currentIndex = index;
  }

  exportArray() {
    this.tutorialService.getAll().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )
    ).subscribe(data => {
      this.tutorials = data;
    });
    TutorialsListComponent.exportArrayToExcel(this.tutorials, "ExampleArray");
  }


  static exportArrayToExcel(arr: TutorialService[], name?: string) {
    let { sheetName, fileName } = getFileName(name);
  
    var wb = XLSX.utils.book_new();
    var ws = XLSX.utils.json_to_sheet(arr);
    XLSX.utils.book_append_sheet(wb, ws, sheetName);
    XLSX.writeFile(wb, `${fileName}.xlsx`);
  }

}