import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { TutorialServiceChecklist } from 'src/app/services/tutorial.serviceChecklist';
import TutorialChecklist from 'src/app/models/tutorialChecklist';

@Component({
  selector: 'app-tutorial-details-checklist',
  templateUrl: './tutorial-details-checklist.component.html',
  styleUrls: ['./tutorial-details-checklist.component.css']
})
export class TutorialDetailsComponentChecklist implements OnInit, OnChanges {

  @Input() tutorial: TutorialChecklist;
  @Output() refreshList: EventEmitter<any> = new EventEmitter();
  currentTutorialChecklist: TutorialChecklist = null;
  message = '';

  constructor(private TutorialServiceChecklist: TutorialServiceChecklist) { }

  ngOnInit(): void {
    this.message = '';
  }

  ngOnChanges(): void {
    this.message = '';
    this.currentTutorialChecklist = { ...this.tutorial };
  }
  
}