import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { ShuttleModelService } from 'src/app/services/shuttle.service';
import ShuttleModel from 'src/app/models/shuttle';

@Component({
  selector: 'servislistesi-checklist',
  templateUrl: './servislistesi-checklist.component.html',
  styleUrls: ['./servislistesi-checklist.component.css']
})
export class ServislistesiChecklistComponent implements OnInit {

  @Input() tutorial: ShuttleModel;
  @Output() refreshList: EventEmitter<any> = new EventEmitter();
  currentTutorialChecklist: ShuttleModel = null;
  message = '';

  constructor(private ShuttleModelService: ShuttleModelService) { }

  ngOnInit(): void {
    this.message = '';
  }

  ngOnChanges(): void {
    this.message = '';
    this.currentTutorialChecklist = { ...this.tutorial };
  }
  
}