import { Component, OnInit } from '@angular/core';
import { ShuttleModelService } from 'src/app/services/shuttle.service';
import { map } from 'rxjs/operators';
import { FilterPipe } from '../../filter.pipe'; 
import * as XLSX from 'xlsx';
import {formatDate} from '@angular/common';


const getFileName = (name: string) => {
  let timeSpan = new Date().toISOString();
  let sheetName = name || "ExportResult";
  let fileName = `${sheetName}-${timeSpan}`;
  return {
    sheetName,
    fileName
  };
};

@Component({
  selector: 'app-servislistesi',
  templateUrl: './servislistesi.component.html',
  styleUrls: ['./servislistesi.component.css']
})
export class ServislistesiComponent implements OnInit {

  shuttles: any;
  currentTutorial = null;
  currentIndex = -1;
  title = '';
  public search:any = '';
  tutorial: any[] = [];
  query:string;
  fileName= 'ExcelSheet.xlsx';
  selectedCount=0;
  startDate: Date;

  getNowDate() {
    var returnDate = "";
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1; 
    var yyyy = today.getFullYear();
    
   
    if (mm < 10) {
        returnDate += `0${mm}-`;
    } else {
        returnDate += `${mm}-`;
    }

    if (dd < 10) {
      returnDate += `0${dd}-`;
  } else {
      returnDate += `${dd}-`;
  }

    returnDate += yyyy;
    return returnDate;
};

  constructor(private shuttleService: ShuttleModelService) { FilterPipe }

  ngOnInit(): void {
    this.retrieveShuttle(this.getNowDate());
  }

  refreshList(): void {
    this.currentTutorial = null;
    this.currentIndex = -1;
    this.retrieveShuttle(this.getNowDate());
  }

  retrieveShuttle(selectedDate): void {
    this.shuttleService.getAll(selectedDate).snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )
    ).subscribe(data => {
      this.shuttles = data;
      for (let i = 0; this.shuttles?.length > i; i++) {
        if(this.shuttles[i].uname!=' ')
        {
          this.selectedCount++;
        }
      }
    });
  }

  setActiveTutorial(tutorial, index): void {
    this.currentTutorial = tutorial;
    this.currentIndex = index;
  }

  
  exportArray() {
   
    this.shuttleService.getAll(this.getNowDate()).snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )
    ).subscribe(data => {
      this.shuttles = data;
    });

    ServislistesiComponent.exportArrayToExcel(this.shuttles, "ExampleArray");
  }

  selectRelatedData(relatedData) {
    let formattedDt = formatDate(this.startDate, 'MM-dd-yyyy', 'en_US')
    console.log(formattedDt.toString());

    this.retrieveShuttle(formattedDt);
    
  }


  static exportArrayToExcel(arr: ShuttleModelService[], name?: string) {
    let { sheetName, fileName } = getFileName(name);
  
    var wb = XLSX.utils.book_new();
    var ws = XLSX.utils.json_to_sheet(arr);
    XLSX.utils.book_append_sheet(wb, ws, sheetName);
    XLSX.writeFile(wb, `${fileName}.xlsx`);
  }
  
}